import { Task } from "../models/Task";
export class TaskController {
    constructor(taskList, taskView) {
        this.taskList = taskList;
        this.taskView = taskView;
        this.taskView.onAddTask = (title) => this.handleAddTask(title);
        this.taskView.onRemoveTask = (id) => this.handleRemoveTask(id);
        this.taskView.onToggleTaskCompletion = (id) => this.handleToggleTaskCompletion(id);
        this.renderTasks();
    }
    handleAddTask(title) {
        if (title.trim()) {
            const task = new Task(title);
            this.taskList.addTask(task);
            this.renderTasks();
        }
    }
    handleRemoveTask(id) {
        this.taskList.removeTask(id);
        this.renderTasks();
    }
    handleToggleTaskCompletion(id) {
        this.taskList.toggleTaskCompletion(id);
        this.renderTasks();
    }
    renderTasks() {
        this.taskView.render(this.taskList);
    }
}
